import { Link, useLocation } from "react-router-dom"

export default function NotFoundComponent() {
    var a = useLocation();

    return (
        <div className="my-box page-not-found">
            <h4>Đường dẫn <strong>{a.pathname}</strong> không tồn tại, nhấn <Link to="/">đây</Link> để về trang chủ </h4>
        </div>
    )
}